.card {
    border-radius: 2px;
    margin: 1rem;
    position: relative;
    text-align: left;
  }

  .card > .img-container {
    height: auto;
    width: auto;
    text-align: center;
    background:#6CADDC;
    zoom: 1.2;
  }
  
  div .card {
      width: 180px;
      height: 170px;
  }