body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.banner {
  width: 100%;
  background: #8E2DE2;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #4A00E0, #8E2DE2);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #4A00E0, #8E2DE2);
  background: linear-gradient(to left, #4A00E0, #8E2DE2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 120px;
  padding-top: 20px;
}

.scoreDiv {
  background: #8E2DE2;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4A00E0, #8E2DE2);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #4A00E0, #8E2DE2);
  background: linear-gradient(to right, #4A00E0, #8E2DE2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100px;
  font-size: 1.25em;
  padding-top: 30px;
  font-weight: bold;
}

li {
  display: inline-block;
  padding: 5px 5px 2px 20px;
  
}

.itemLeft {
  width: 35%;
}

.itemCenter {
  width: 35%;
}

.itemRight {
  width: 15%;
}

.wrapper {
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  overflow: auto;
  justify-content: inherit;
  
}

body {
  background: rgb(2, 2, 88);
}





.card {
    border-radius: 2px;
    margin: 1rem;
    position: relative;
    text-align: left;
  }

  .card > .img-container {
    height: auto;
    width: auto;
    text-align: center;
    background:#6CADDC;
    zoom: 1.2;
  }
  
  div .card {
      width: 180px;
      height: 170px;
  }
